<template>
  <div class="comments-wrap">

  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'comments',
  data: () => ({

  }),
  created () {
    axios.get(`/wordpress-4.7.4/wp-json/wp/v2/comments/`, {
      params: {
        post: this.$route.params.id,
        page: 1,
        per_page: 10
      }
    }).then((res) => console.log(res)).catch((err) => console.log(err))
  }
}
</script>
<style lang="scss" scoped>
</style>



